.header {
  background: #282c32;
  min-height: 70px;
  box-shadow: 0 0 5px 1px rgb(18, 233, 233);
  position: fixed;
  color: #61dafb;
  font-size: calc(10px + 14 * (100vw - 320px)/ 800);
  font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;
  z-index: 99;
  white-space: nowrap;
  width: 100%;
  margin-bottom: 70px;
}

.header ul {
  list-style-type: none;
  margin: 0 auto;
  padding: 0;
  width: 100%;
  height: 100%;
  text-align: center;
}

.header li {
  display: inline-block;
  width: 33%;
  padding-top: 10px;
}

.header li a {
  color: #61dafb;
  text-decoration: none;
}

.header li a:hover {
  color: #4499b1;
  text-decoration: none;
}

.reactLogo {
  height: 30px;
  width: 30px;
  -webkit-animation:spin 10s linear infinite;
  animation:spin 10s linear infinite;
}
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

.instructotron {
    background-color: #4c699c;
    /* Source for background image: https://wallpapersmug.com/w/download/3840x2400/digital-art-circuit-abstract-63499a */
    background-image: url(/React-Concentration-Game/static/media/InstructotronBackground.fa065a4f.jpg);
    background-size: cover;
    background-repeat: repeat;
    opacity: 0.9;
    color: #fff;
    font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;
    min-height: 300px;
    padding: 120px 20px;
    text-align: center;
}

.game-title {
    font-size: calc(30px + 14 * (100vw - 320px)/ 800);
}

.game-instructions {
    font-size: calc(10px + 14 * (100vw - 320px)/ 800);
}
.wrapper {
  padding-top: 80px;
  padding-bottom: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  min-height: 80%;
  flex-wrap: wrap;
  flex-flow: row wrap;
  justify-content: center;
  align-content: flex-start;
  overflow: auto;
  margin: 0, auto;
}
@media (min-width: 1100px) {
  .wrapper{
    padding-left: 100px;
    padding-right: 100px;
    margin-left: 170px;
    margin-right: 170px;
    width: 1100px;
  } 
}

.card {
  background: #fff;
  border-radius: 0.5rem;
  height: 170px;
  width: 170px;
  margin: 8px 25px;
  border: 3px solid #61dafb;
  background-size: cover;
  position: relative;
  overflow: hidden;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: center;
}

.card:hover {
  border: 3px solid darkslategrey;
}

@media (max-width: 880px) {
  .card {
      height: 150px;
      width: 150px;
  }
}

@media (max-width: 800px) {
  .card {
      height: 130px;
      width: 130px;
  }
}

@media (max-width: 750px) {
  .card {
      height: 120px;
      width: 120px;
  }
}

@media (max-width: 750px) {
  .card {
      height: 100px;
      width: 100px;
  }
}

@media (max-width: 450px) {
  .card {
      height: 75px;
      width: 75px;
  }
}
.footer {
    min-height: 30px;
    width: 100%;
    background: #3d434d;
    box-shadow: 0 0 5px 1px rgb(18, 233, 233);
    position: relative;
    margin: 0 auto;
    color: #61dafb;
    font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;
    font-size: calc(2px + 6 * (100vw - 10px)/ 700);
    font-style: oblique;
}

.bottom {
    position: absolute;
    bottom: 0;
    margin: 0 auto;
    width: 100%;
    min-height: 30px;
    background: #1d1f24;
    text-align: center;
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

body {
  background-color: #ECEFF1;
}
